body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
/*  animation: App-logo-spin infinite 20s linear;*/
  height: 10vmin;
  pointer-events: none;
  margin-left: 2%;
}

.App-header {
  background-color: #282c34;
  display: -webkit-flex;
  display: flex;
  min-height: 12vh;
  width: 100%;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  font-size: calc(10px + 2vmin);
  color: white;
}

body {
  margin-bottom: 5%;
}

.button {
  background-color: #3e8e41;
  color: white;
  margin: 2%;
  cursor: pointer;
  border-radius: 20px;
  font-size: 20px;
}

.button:hover {
  background-color: green;
}

.container {
  display: grid;
  margin: 2%;
  text-align: left;
  border: 5px;
  border-style: solid;
  border-color: #bfbfbf;
  background-color: #feffdd;
  padding: 1%;
  color: #2d5929;
  width: 15em;
}

.delete {
  margin-top: 5%;
  width: 4em;
  background-color: #77abff;
  text-align: center;
  padding: 0.2em;
  border-radius: 20px;
  color: white;
  cursor: pointer;
}

.delete:hover {
  background-color: #510000;
}

footer {
  background-color: #282c34;
  height:20px;
  width: 100%;
  color:  #FFDAB9;
  text-align: center;
  bottom: 0;
  position: fixed;
}

input {
  margin: 5px;
}

